<template>
  <div :id="id" style="background: white;"></div>
</template>

<script>
import G2 from '@antv/g2'
import util from './utils'

export default {
  name: 'Bar',
  props: {
    height: {
      default: 400,
      type: Number
    }
  },
  data () {
    return {
      id: util.id(),
      chart: null,
      data: [{
        type: '汽车',
        value: 34
      }, {
        type: '建材家居',
        value: 85
      }, {
        type: '住宿旅游',
        value: 103
      }, {
        type: '交通运输与仓储邮政',
        value: 142
      }, {
        type: '建筑房地产',
        value: 251
      }, {
        type: '教育',
        value: 367
      }, {
        type: 'IT 通讯电子',
        value: 491
      }, {
        type: '社会公共管理',
        value: 672
      }, {
        type: '医疗卫生',
        value: 868
      }, {
        type: '金融保险',
        value: 1234
      }]
    }
  },
  mounted () {
    this.reload()
  },
  methods: {
    reload () {
      let chart = this.chart
      if (chart != null) {
        chart.destory()
      }
      chart = this.chart = new G2.Chart({
        container: this.id,
        forceFit: true,
        height: this.height,
        padding: [20, 40, 50, 124]
      })
      chart.source(this.data, {
        value: {
          max: 1300,
          min: 0,
          nice: true,
          alias: '销量（百万）'
        }
      })
      chart.axis('type', {
        label: {
          textStyle: {
            fill: '#8d8d8d',
            fontSize: 12
          }
        },
        tickLine: {
          alignWithLabel: false,
          length: 0
        },
        line: {
          lineWidth: 0
        }
      })
      chart.axis('value', {
        label: null,
        title: {
          offset: 30,
          textStyle: {
            fontSize: 12,
            fontWeight: 300
          }
        }
      })
      chart.legend(false)
      chart.coord().transpose()
      chart.interval().position('type*value').opacity(1).label('value', {
        textStyle: {
          fill: '#8d8d8d'
        },
        offset: 10
      })
      chart.render()
    }
  }
}
</script>

<style scoped>

</style>



