<template>
  <div :id="id" style="background: white;"></div>
</template>

<script>
import G2 from '@antv/g2'
import util from './utils'

export default {
  name: 'Column',
  props: {
    height: {
      default: 400,
      type: Number
    }
  },
  data () {
    return {
      id: util.id(),
      chart: null,
      data: [{
        'label': 'Mon.',
        'type': 'series1',
        'value': 2800
      }, {
        'label': 'Mon.',
        'type': 'series2',
        'value': 2260
      }, {
        'label': 'Tues.',
        'type': 'series1',
        'value': 1800
      }, {
        'label': 'Tues.',
        'type': 'series2',
        'value': 1300
      }, {
        'label': 'Wed.',
        'type': 'series1',
        'value': 950
      }, {
        'label': 'Wed.',
        'type': 'series2',
        'value': 900
      }, {
        'label': 'Thur.',
        'type': 'series1',
        'value': 500
      }, {
        'label': 'Thur.',
        'type': 'series2',
        'value': 390
      }, {
        'label': 'Fri.',
        'type': 'series1',
        'value': 170
      }, {
        'label': 'Fri.',
        'type': 'series2',
        'value': 100
      }]
    }
  },
  mounted () {
    this.reload()
  },
  methods: {
    reload () {
      let chart = this.chart
      if (chart != null) {
        chart.destory()
      }
      chart = this.chart = new G2.Chart({
        container: this.id,
        forceFit: true,
        height: this.height
      })
      chart.source(this.data)
      // 控制 bar & column 转向
      // chart.coord().transpose().scale(1, -1)
      // adjust 控制 并排还是累加
      chart.interval().position('label*value').color('type').adjust([{
        type: 'dodge',
        marginRatio: 1 / 32
      }])
      chart.render()
    }
  }
}
</script>

<style scoped>

</style>



