<template>
  <div :id="id" style="background: white;"></div>
</template>

<script>
import G2 from '@antv/g2'
import util from './utils'

export default {
  name: 'Line',
  data () {
    return {
      id: util.id(),
      chart: null,
      data: []
    }
  },
  mounted () {
    this.reload()
  },
  methods: {
    reload () {
      let chart = this.chart
      if (chart != null) {
        chart.destory()
      }
      chart = this.chart = new G2.Chart({
        container: this.id,
        forceFit: true,
        height: 400
      })
      let data = this.data
      chart.source(data, {
        time: {
          alias: '时间',
          type: 'time',
          mask: 'mm:ss',
          tickCount: 10,
          nice: true
        },
        temperature: {
          alias: '平均温度(°C)',
          min: 10,
          max: 35
        },
        type: {
          type: 'cat'
        }
      })
      // ['#ff7f0e', '#2ca02c']
      chart.line().position('time*temperature').color('type', util.Color.getColors()).shape('smooth').size(2)
      chart.render()
      setInterval(function () {
        var now = new Date()
        var time = now.getTime()
        var temperature1 = ~~(Math.random() * 5) + 22
        var temperature2 = ~~(Math.random() * 7) + 17
        if (data.length >= 200) {
          data.shift()
          data.shift()
        }
        data.push({
          time: time,
          temperature: temperature1,
          type: '记录1'
        })
        data.push({
          time: time,
          temperature: temperature2,
          type: '记录2'
        })
        chart.changeData(data)
      }, 1000)
    }
  }
}
</script>

<style scoped>

</style>



